import React, { useEffect } from 'react';
import { useParams } from 'react-router-dom';
import {connect} from 'react-redux';

import * as actionCreators from '../../store/actions/index';

import Spinner from '../../components/UI/Spinner/Spinner';
import {
	getSpreadSheetUrlWithRanges,
	getTaskPathByPageId,
	pathSheetIdPairs,
	mimeTypes,
} from '../../../constants'; 

import './DragAndDrop.css';

const { html } = mimeTypes;

const shiftElement = id => {
	const  elem = document.querySelector(`[data-id="${id}"]`);

	return elem.parentNode.removeChild(elem);
};

const checkIfCorrect = e => {
	if (e.target.firstElementChild.dataset.id === e.target.parentNode.dataset.value) {
	  e.target.classList.add('-correct');
	  e.target.parentNode.classList.add('-correct');
	  e.target.firstElementChild.draggable = false;
	} else {
	  e.target.classList.add('-wrong');
	}
};

const onDragStartHandler = e => {
	e.target.classList.add('-dragged');
	e.dataTransfer.setData(html, e.target.dataset.id);
	e.dataTransfer.effectAllowed = 'move';
};

const onDragEndHandler = e => {
	e.target.classList.remove('-dragged');
};

const onDragOverHandler = e => {
	if(e.dataTransfer.types[0] === html) {
	 	e.preventDefault();
		if (
			e.target.classList.contains('droppable')
			&& !(e.target.classList.contains('-correct')
			|| e.target.classList.contains('-wrong'))
		) {
			e.target.classList.add('-active');
	  	}
	}
  };

  const onDragLeaveHandler = e => {
	[...document.querySelectorAll('.droppable')].forEach((el) => {
		if (!el.children.length) {
			el.classList.remove('-active', '-correct', '-wrong');
		}
	});
  };

  const onDropHandler = (e) => {
	const wasDroppedBackInWordsList = e.target.closest('.draggable-container');
	const droppableContainer = wasDroppedBackInWordsList || e.target.closest('.box:not(.-wrong):not(.-correct)');
	if(!droppableContainer) return;

	const id = e.dataTransfer.getData(html);
	droppableContainer.appendChild(shiftElement(id));
	onDragLeaveHandler();
	if (!wasDroppedBackInWordsList) {
		checkIfCorrect(e);
		e.target.classList.remove('-active'); 
	}
  };

/**
 * Renders Drag and Drop game component.
 *
 * @param {object} props - Props.
 * @param {Array.<object>} data - Data for task.
 * @param {string|null} error - Error.
 * @param {boolean} loading - Show loader or not.
 * @param {Function} fetchDataConnect - Action that fetches data.
 * @return {JSX.Element} - Drag and Drop game component or loader. 
 */
const DragAndDrop = (props) => {
	const {
		fetchDataConnect,
		data,
		loading,
		title,
	} = props;
	const { token } = useParams();
console.log(props);
	useEffect(() => {
		const pageId = pathSheetIdPairs['/drag-and-drop'];
		const requestUrl = getTaskPathByPageId(pageId);
console.log(token);
		fetchDataConnect(
			getSpreadSheetUrlWithRanges(requestUrl, token)
		);
	}, []);


	if (loading) {
		return  <Spinner />;
	}
	if (data) {
		return (
			<div className="DragAndDrop">
				{title ? (
					<h1>{title}</h1>
				) : null}
				<div className="drag-and-drop"
					onDragStart={onDragStartHandler}
					onDragEnd={onDragEndHandler}
					onDragEnter={onDragOverHandler}
					onDragLeave={onDragLeaveHandler}
					onDragOver={onDragOverHandler}
					onDrop={onDropHandler}
				>
					<div className="draggable-container">
						{data.map(el => (
							<span
								className="element"
								data-id={el.word}
								draggable
								key={el.word}
							>
								{el.word}
							</span>
						))}
					</div>
					<ol className="droppable-container">
						{data
							.filter(el => el.sentence ? el.sentence : null)
							.map((el) => (
								<li
									className="sentense"
									data-value={el.correctWord}
									key={el.correctWord}
								>
									{el.sentence.split('|+|')[0]}
									<span className="box droppable" />
									{el.sentence.split('|+|')[1]}
								</li>
						))}
					</ol>
				</div>
			</div>
		);
	}
};

const mapStateToProps = state => {
	return {
		data: state.wordsConstructor.fetchedData,
		loading: state.wordsConstructor.loading,
		error: state.wordsConstructor.error
	};
};

const mapDispatchToProps = dispatch => {
	return {
	  fetchDataConnect: url => dispatch(actionCreators.asyncFetchStart(url))
	};
  };

export default connect(mapStateToProps, mapDispatchToProps)(DragAndDrop);
